<template>
  <div class="video-share">
    <div id="video_player"></div>
    <img v-if="showPlayBtn" @click="doPlay" src="../live-share/img/play@2x.png" alt="" id="play_btn" class="play">
    <div class="data-num" @click.stop="download">
      <img :src="author.avatar || $store.state.defaultAvatar" alt="" class="avatar">
      <div class="num">
        <img src="./zan@3x.png" alt="">
        <p>{{ detail.like_count }}</p>
      </div>
      <div class="num">
        <img src="./message@3x.png" alt="">
        <p>{{ detail.comment_count }}</p>
      </div>
      <div class="num">
        <img src="./zhuanfa@3x.png" alt="">
        <p>{{ detail.share_count }}</p>
      </div>
      <div class="num">
        <img src="./logo@2x.png" alt="">
        <p>打开APP</p>
      </div>
    </div>
    <div class="user-info">
      <div class="top">
        <p>{{ author.nick_name }}</p>
        <button @click.stop="download">+关注</button>
      </div>
      <p class="text-overflow">{{ detail.title }}</p>
    </div>
  </div>
</template>

<script>
import { post } from '../../common/js/http'

export default {
  name: 'video-share',
  data() {
    return {
      showPlayBtn: true,
      detail: {},
      author: {}
    }
  },
  created () {
    this.getDetail()
  },
  mounted () {
    // this.createPlayer()
  },
  beforeDestroy () {
    document.removeEventListener('WeixinJSBridgeReady', this.wxPlay)
    document.body.removeEventListener('touchstart', this.doPlay)
  },
  methods: {
    createPlayer() {
      this.player = new window.TcPlayer('video_player', {
        width: '100%',
        height: '100%',
        poster: { src: this.detail.thumb_url, style: 'cover' },
        controls: 'none',
        autoplay: false,
        // rePlay: true,
        x5_video_position: 'center',
        x5_type: 'h5',
        mp4: this.detail.play_url.indexOf('mp4') >= 0 ? this.detail.play_url : '',
        m3u8: this.detail.play_url.indexOf('m3u8') >= 0 ? this.detail.play_url : ''
      })

      document.addEventListener('WeixinJSBridgeReady', this.wxPlay)

      document.body.addEventListener('touchstart', this.doPlay)
    },
    doPlay() {
      if (this.player.playing()) return
      this.player.play()
      setTimeout(() => {
        if (this.player.playing()) {
          this.showPlayBtn = false
        }
      }, 200)
    },
    wxPlay() {
      this.player.play()
      setTimeout(() => {
        if (this.player.playing()) {
          this.showPlayBtn = false
        }
      }, 200)
    },
    getDetail() {
      post('/webapi/Shortvideo/getShortvideoInfo', { id: this.$route.params.id })
        .then(({ data: { data } }) => {
          this.detail = data
          this.author = data.author
          this.$nextTick(() => {
            this.createPlayer()
          })
        })
    },
    download() {
      window.location.href = '/download'
    }
  }
}
</script>

